<template>
  <div class="page">
    <div class="page__content">
      <div class="page__header">
        <h1 class="page__title">
          Площадки
        </h1>
      </div>

      <div class="place-cards">
        <place-card
          v-for="place in places"
          :place="place"
          :key="place.id"
          :id="place.id"
        >
        </place-card>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import PlaceCard from "@/components/Cards/PlaceCard";

export default {
  name: "PlacesPage",
  components: { PlaceCard },
  data() {
    return {
      places: []
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/place/list`);

      await axios
        .get(url)
        .then(response => {
          this.places = response.data._items;
          load.hide();
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          this.$toasted.global.error(error.response.data.message);
          load.hide();
        });
    }
  }
};
</script>

<style scoped></style>
